import React, { useEffect, useState } from "react";

interface PropProperties {
    cv: any;
    print: boolean;
}

const Social: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv, print } = props;
    const [socials, setSocials] = useState<any[]>([]);

    useEffect(() => {
        setSocials(cv?.socials || []);
    }, [cv]);

    return (
        <div>
            {print &&
                socials.map((social: any, i: number) => (
                    <div className="information" key={`social--${i}`}>
                        {social.link ? (
                            <span>
                                <i className={social.icon} />
                                &nbsp;
                                <a
                                    href={social.link}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    {social.desc}
                                </a>
                            </span>
                        ) : (
                            <span className="pl-5">{social.desc}</span>
                        )}
                    </div>
                ))}
        </div>
    );
};

export default Social;
