import React from "react";
import vi from "src/static/vi.svg";
import en from "src/static/en.svg";

interface PropProperties {
    print: boolean;
    language: string;
    page: number;
    setPage: (page: number) => void;
    doPrint: (event: any) => void;
    setLanguage: (language: string) => void;
}

const Toolbar: React.FC<PropProperties> = (props: PropProperties) => {
    const { print, language, page, setPage, doPrint, setLanguage } = props;

    return (
        <>
            {!print ? (
                <aside className="vc--toolbar">
                    <ul>
                        <li>
                            <span
                                className="cursor p-3 d-block"
                                onClick={doPrint}
                            >
                                <i className="fa fa-print" />
                                &nbsp;
                            </span>
                        </li>
                        <li
                            onClick={() =>
                                setLanguage(language === "vi" ? "en" : "vi")
                            }
                        >
                            <span className="cursor p-3 d-block">
                                <img
                                    className="img-thumbnail vc--resume-flag"
                                    src={language === "vi" ? en : vi}
                                    alt=""
                                />
                            </span>
                        </li>
                        <li>
                            {page === 2 ? (
                                <span
                                    className="cursor p-3 d-block"
                                    onClick={() => setPage(1)}
                                >
                                    <i className="fas fa-backward" />
                                </span>
                            ) : (
                                <span
                                    className="cursor p-3 d-block"
                                    onClick={() => setPage(2)}
                                >
                                    <i className="fas fa-forward" />
                                </span>
                            )}
                        </li>
                    </ul>
                </aside>
            ) : (
                ""
            )}
        </>
    );
};

export default Toolbar;
