import React, { useEffect, useState } from "react";

interface PropProperties {
    cv: any;
    print: boolean;
}

const ContactAdress: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv, print } = props;
    const [address, setAddress] = useState<any[]>([]);

    useEffect(() => {
        setAddress(cv?.contact?.address || []);
    }, [cv]);

    return (
        <>
            {print && address ? (
                <div>
                    {address.map((info: any, i: number) => (
                        <div
                            className="information"
                            key={`contact-address--${i}`}
                        >
                            <span>
                                <i className={info.icon} />
                                &nbsp;
                                {info.link ? (
                                    <a
                                        href={info.link}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        {info.desc}
                                    </a>
                                ) : (
                                    <span className="pl-5">{info.desc}</span>
                                )}
                            </span>
                        </div>
                    ))}
                </div>
            ) : (
                ""
            )}
        </>
    );
};

export default ContactAdress;
