import React, { useEffect, useState } from "react";
import Translator from "src/components/Translator";

interface PropProperties {
    cv: any;
    print: boolean;
}

const Skill: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv } = props;
    const [skills, setSkills] = useState<any[]>([]);

    useEffect(() => {
        setSkills(cv?.skillsDesc || []);
    }, [cv]);

    return (
        <>
            {skills ? (
                <section className="skill-desc">
                    <div className="title-wrap d-flex align-items-center">
                        <i className="fa fa-wrench" />
                        <span className="pl-5">
                            <Translator text="skills" />
                        </span>
                    </div>
                    <div className="content">
                        <ul className="skill-desc">
                            {skills &&
                                skills.map((skill: any, i: any) => (
                                    <li key={`skill--desc-${i}`}>
                                        <i className="fa fa-certificate" />
                                        &nbsp;{skill}
                                    </li>
                                ))}
                        </ul>
                    </div>
                </section>
            ) : (
                ""
            )}
        </>
    );
};

export default Skill;
