import React from "react";
import Translator from "src/components/Translator";

interface Project {
    name: string;
    description: string[];
}

interface ProjectDesc {
    link: string;
    title: string;
    text: string;
}

interface Item {
    time: string;
    title: string;
    name?: string;
    company?: string;
    source: string;
    online: string;
    projects: Project[];
    description?: string[] | ProjectDesc[];
}

interface PropProperties {
    items: Item[];
    print: boolean;
    page: number;
}

const Timeline: React.FC<PropProperties> = (props: PropProperties) => {
    const { items, page, print } = props;

    return (
        <div className="timeline">
            {items &&
                items.map((item: any, i: any) => (
                    <div
                        className="timeline-item"
                        key={`timeline--wrapper--${i}`}
                    >
                        <div className="timeline-item-time">{item.time}</div>
                        <div className="timeline-item-project">
                            <div className="circle" />
                            <div className="project-info">
                                <span className="project-name">
                                    {item.company || item.name}
                                </span>
                                <span className="work-title">{item.title}</span>
                                <span className="project-time">
                                    {item.time}
                                </span>
                                <span className="project-link">
                                    {item.source}
                                </span>
                                <a
                                    className="project-code"
                                    href={item.source}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <i className="fa fa-link" />
                                    &nbsp;
                                    {print ? (
                                        item.source
                                    ) : (
                                        <Translator text="source" />
                                    )}
                                </a>
                                <a
                                    className="project-code"
                                    href={item.online}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <i className="fa fa-link" />
                                    &nbsp;
                                    {print ? (
                                        item.online
                                    ) : (
                                        <Translator text="online" />
                                    )}
                                </a>
                            </div>
                            {page === 1 ? (
                                <ul className="work-project-list">
                                    {item.projects &&
                                        item.projects
                                            .reverse()
                                            .map((wp: any, i: any) => (
                                                <li key={`project----${i}`}>
                                                    <h4>{wp.name}</h4>
                                                    {wp.description.length ? (
                                                        <ul className="project-desc">
                                                            {wp.description &&
                                                                wp.description.map(
                                                                    (
                                                                        desc: any,
                                                                        i: any
                                                                    ) => (
                                                                        <li
                                                                            key={`wp--description--${i}`}
                                                                        >
                                                                            <i className="fa fa-certificate" />
                                                                            &nbsp;
                                                                            {
                                                                                desc
                                                                            }
                                                                        </li>
                                                                    )
                                                                )}
                                                        </ul>
                                                    ) : (
                                                        ""
                                                    )}
                                                </li>
                                            ))}
                                </ul>
                            ) : (
                                <ul className="project-desc">
                                    {item.description &&
                                        item.description.map(
                                            (desc: any, i: any) => (
                                                <li
                                                    key={`item--description--${i}`}
                                                >
                                                    <i className="fa fa-certificate" />
                                                    {typeof desc ===
                                                    "object" ? (
                                                        <span className="pl-5">
                                                            <span className="other-text">
                                                                {desc.title}
                                                                --
                                                                {desc.text}
                                                            </span>
                                                            <a
                                                                className="project-code"
                                                                href={desc.link}
                                                                target="_blank"
                                                                rel="noreferrer"
                                                            >
                                                                <i className="fa fa-link" />
                                                                &nbsp;
                                                                {print ? (
                                                                    desc.link
                                                                ) : (
                                                                    <Translator text="source" />
                                                                )}
                                                            </a>
                                                        </span>
                                                    ) : (
                                                        <span className="pl-5">
                                                            {desc}
                                                        </span>
                                                    )}
                                                </li>
                                            )
                                        )}
                                </ul>
                            )}
                        </div>
                    </div>
                ))}
        </div>
    );
};

export default Timeline;
