import React, { useEffect, useState } from "react";
import "src/app/style.css";
import { request } from "src/service/request";
import Content from "src/components/Content";
import Print from "src/components/Print";
import Toolbar from "src/components/Toolbar";
import TopCV from "src/static/resume.json";
import i18n from "src/i18n";

const jsonUri = (window as any).__URL__ || null;

const App: React.FC = () => {
    const defaultProfile = Object.create(TopCV);

    const [print, setPrint] = useState(false);
    const [profile, setProfile] = useState(defaultProfile);
    const [page, setPage] = useState(1);
    const [cv, setCv] = useState(defaultProfile.vi);
    // listen on end print event
    (window as any).onafterprint = (event: any) => {
        setPrint(false);
    };

    // mount
    useEffect(() => {
        if (jsonUri) {
            request(jsonUri)
                .then((profile: any) => {
                    if (profile) {
                        setProfile(profile);
                    }
                })
                .catch((e: any) => {});
        }
    }, []);

    // change profile by language
    useEffect(() => {
        setCv(
            profile && profile[i18n.language]
                ? profile[i18n.language]
                : defaultProfile.vi
        );
    }, [profile, defaultProfile]);

    // print effect
    useEffect(() => {
        if (print) {
            (window as any).print();
        }
    }, [print]);

    return (
        <div className="vc--resume" role="application">
            <div className="row">
                <div className="vc--resume col-12">
                    {print ? (
                        <Print
                            cv={cv}
                            page={page}
                            print={print}
                            setPage={(p: number) => setPage(p)}
                        />
                    ) : (
                        <Content
                            cv={cv}
                            page={page}
                            print={print}
                            setPage={(p: number) => setPage(p)}
                        />
                    )}
                </div>
                <Toolbar
                    print={print}
                    page={page}
                    setPage={(p: number) => setPage(p)}
                    language={i18n.language}
                    doPrint={(event: any) => {
                        setPrint(true);
                    }}
                    setLanguage={(lang: string) => i18n.changeLanguage(lang)}
                />
            </div>
        </div>
    );
};

export default App;
