import React, { useEffect, useState } from "react";
import Translator from "src/components/Translator";
import Timeline from "src/components/Timeline";

interface PropProperties {
    cv: any;
    print: boolean;
}

const Project: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv, print } = props;
    const [projects, setProjects] = useState<any[]>([]);

    useEffect(() => {
        setProjects(cv?.projects || []);
    }, [cv]);

    return (
        <>
            {projects ? (
                <section className="projects">
                    <div className="title-wrap d-flex align-items-center">
                        <i className="fa fa-file-alt" />
                        <span className="pl-5">
                            <Translator text="projects" />
                        </span>
                    </div>
                    <div className="content">
                        <Timeline items={cv.projects} page={2} print={print} />
                    </div>
                </section>
            ) : (
                ""
            )}
        </>
    );
};

export default Project;
