import React from "react";
import Header from "src/components/Header";
import Footer from "src/components/Footer";
import PageOne from "src/components/PageOne";
import PageTwo from "src/components/PageTwo";

interface PropProperties {
    cv: any;
    print: boolean;
    page: number;
    setPage: (p: number) => void;
}

const Print: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv, print, page, setPage } = props;

    return (
        <main className="vc--resume-content row">
            <Header cv={cv} print={print} />

            <PageOne cv={cv} setPage={(p) => setPage(p)} print={print} />
            <PageTwo cv={cv} setPage={(p) => setPage(p)} print={print} />

            <Footer page={page} setPage={(p) => setPage(p)} print={print} />
        </main>
    );
};

export default Print;
