import React, { useEffect, useState } from "react";
import Translator from "src/components/Translator";

interface PropProperties {
    cv: any;
    print: boolean;
}

const Education: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv } = props;
    const [educations, setEducations] = useState<any[]>([]);

    useEffect(() => {
        setEducations(cv?.educations || []);
    }, [cv]);

    return (
        <>
            {educations ? (
                <section className="education">
                    <div className="title-wrap d-flex align-items-center">
                        <i className="fa fa-book" />
                        <span>
                            <Translator text="educations" />
                        </span>
                    </div>
                    <div className="mt-3 ml-4">
                        {educations.map((education: any, i: number) =>
                            Object.values(education).map(
                                (value: any, j: number) => (
                                    <span key={`educations----${i}-${j}`}>
                                        {value}&nbsp;
                                    </span>
                                )
                            )
                        )}
                    </div>
                </section>
            ) : (
                ""
            )}
        </>
    );
};

export default Education;
