import React, { useEffect, useState } from "react";
import Translator from "src/components/Translator";
import Timeline from "src/components/Timeline";

interface PropProperties {
    cv: any;
    print: boolean;
}

const Work: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv, print } = props;
    const [works, setWorks] = useState<any[]>([]);

    useEffect(() => {
        setWorks(cv.works || []);
    }, [cv]);

    return (
        <>
            {works ? (
                <section className="works">
                    <div className="title-wrap d-flex align-items-center">
                        <i className="fa fa-briefcase" />
                        <span className="pl-5">
                            <Translator text="works" />
                        </span>
                    </div>
                    <div className="content">
                        <Timeline items={works} page={1} print={print} />
                    </div>
                </section>
            ) : (
                ""
            )}
        </>
    );
};

export default Work;
