import React from "react";
import { useDeviceDetect } from "src/hooks/useDeviceDetect";
import Left from "src/components/Left";
import Project from "src/components/Project";
import Skill from "src/components/Skill";

interface PropProperties {
    cv: any;
    print: boolean;
    setPage: (p: number) => void;
}

const PageTwo: React.FC<PropProperties> = (props: PropProperties) => {
    const { cv, setPage, print } = props;

    const device = useDeviceDetect();

    return (
        <article
            className="active"
            onClick={() => device.isMobile() && setPage(1)}
        >
            <div className="row">
                {!print ? (
                    <div className="col-3 p-0">
                        <Left klassName="col-md-3-3" cv={cv} />
                    </div>
                ) : (
                    ""
                )}
                <div className={`right ${print ? "col-12 ml-4" : "col-9"}`}>
                    <Project cv={cv} print={print} />
                    <Skill cv={cv} print={print} />
                </div>
            </div>
        </article>
    );
};

export default PageTwo;
